<template>
  <div class="CollegeInformation padding students">
    <!-- 学院管理 -->
    <!-- 弹框组件 -->
    <el-dialog
      :title="tianjia"
      :visible.sync="dialogFormVisible"
      :before-close="handleClose"
      :close-on-click-modal="false"
    >
      <el-form :model="form" :rules="rules" ref="ruleForm">
        <el-form-item label="学院/部门代码：" :label-width="formLabelWidth" prop="code">
          <el-input v-model="form.code" auto-complete="off" placeholder="请输入学院/部门代码"></el-input>
        </el-form-item>
        <el-row>
          <el-col :span="12">
            <el-form-item label="学院/部门类型：" :label-width="formLabelWidth" prop="creatorVal">
              <el-select v-model="form.creatorVal" placeholder="请选择学院/部门类型" filterable>
                <el-option
                  v-for="item in options"
                  :key="item.code"
                  :label="item.value"
                  :value="item.code"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>

        <el-form-item label="学院/部门名称：" :label-width="formLabelWidth" prop="name">
          <el-input v-model="form.name" auto-complete="off" placeholder="请输入学院/部门名称"></el-input>
        </el-form-item>
        <el-form-item label="学院/部门简介：" :label-width="formLabelWidth" prop="intro">
          <el-input v-model="form.intro" auto-complete="off" type="textarea" :rows="4" placeholder="请输入学院/部门简介"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="resetForm('ruleForm')">取 消</el-button>
        <el-button type="primary" @click="seccons('ruleForm')" v-show="falg1">确 定</el-button>
        <el-button type="primary" @click="seccons1('ruleForm')" v-show="falg">确 定</el-button>
      </div>
    </el-dialog>
    <crumb style="padding: 0 5px;">
      <div class="new--btn" slot="right" @click="close">+ 新建</div>
    </crumb>
    <div class="CollegeInformation-tab">
      <!-- <div class="examination-dv">
        <img src="@/assets/mao.png" alt />
        <span>学院信息</span>
      </div> -->
      <div class="CollegeInformation-inp">
        <el-form :inline="true" :model="formInline" class="demo-form-inline">
          <el-form-item label="学院/部门搜索：">
            <el-input v-model="formInline.user" placeholder="学院/部门名称">
              <!-- <i slot="suffix" class="el-icon-search" @click="onSubmit"></i> -->
            </el-input>
          </el-form-item>
          <el-form-item>
            <div class="search--btn"  @click="onSubmit">搜索</div>
          </el-form-item>
        </el-form>
      </div>
      <template>
        <el-table :data="tableData1" stripe v-loading="loading">
          <el-table-column label="学院/部门代码">
            <template slot-scope="scope">
              <span style="margin-left: 5px">{{ scope.row.code }}</span>
            </template>
          </el-table-column>
          <el-table-column label="学院/部门名称">
            <template slot-scope="scope">
              <span style="margin-left: 5px">{{ scope.row.name }}</span>
            </template>
          </el-table-column>
          <el-table-column label="学院/部门类型">
            <template slot-scope="scope">
              <span style="margin-left: 5px;" v-if="scope.row.academyType == 1">{{ `学院` }}</span>
              <span style="margin-left: 5px" v-else>{{ `部门` }}</span>
            </template>
          </el-table-column>
          <el-table-column label="学院/部门简介" width="540">
            <template slot-scope="scope">
              <el-popover
                placement="top-start"
                width="600"
                :content="scope.row.intro"
              ><div slot="reference">{{ scope.row.intro }}</div></el-popover>
              <!-- <div style="margin-left: 5px;width: 600px;overflow: hidden;white-space: nowrap;text-overflow: ellipsis;cursor: pointer;" v-popover:popover1
              >{{ scope.row.intro }}</div> -->
            </template>
          </el-table-column>
          <el-table-column label="操作" width="150">
           <template slot-scope="scope">
              <el-button type="text" size="small" @click="handleEdit(scope.$index, scope.row)">
                <SvgIcon icon-class="iconbianji1"></SvgIcon>&nbsp;&nbsp;编辑
              </el-button>
              <el-button style="color: #ff5858;" type="text" size="small" @click="handleDelete(scope.$index, scope.row)">
                <SvgIcon icon-class="iconshanchu"></SvgIcon>&nbsp;&nbsp;删除
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>
    </div>
    <div>
      <template>
        <div class="block">
          <el-pagination
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage1"
            :page-sizes="[15, 30, 40, 50]"
            :page-size="pageSize"
            layout="prev, pager, next, jumper, sizes, total"
            :total="total"
          ></el-pagination>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import crumb from '@/components/Crumb'
export default {
  components: {
    crumb
  },
  data() {
    return {
      dialogFormVisible: false,
      formLabelWidth: "140px",
      falg: false,
      falg1: false,
      currentPage1: 1,
      total: 0,
      pageSize: 15,
      pageNum: 1,
      tianjia: "",
      options: [
        {
          code: 1,
          value: "学院"
        },
        {
          code: 2,
          value: "部门"
        }
      ],
      form: {
        name: "",
        code: "",
        intro: "",
        creatorVal: ""
      },
      rules: {
        code: [
          {
            required: true,
            message: "学院/部门代码不能为空",
            trigger: "blur"
          }
        ],
        name: [
          {
            required: true,
            message: "学院/部门名称不能为空",
            trigger: "blur"
          }
        ],
        intro: [
          {
            required: true,
            message: "学院/部门简介不能为空",
            trigger: "blur"
          }
        ],
        creatorVal: [
          {
            required: true,
            message: "学院/部门类型不能为空",
            trigger: "blur"
          }
        ]
      },
      formInline: {
        user: ""
      },
      tableData: [],
      loading: true,
      tableData1: []
    };
  },
  methods: {
    academyType() {
      this.$get("system/dict/field/academyType").then(res => {
        this.options = res.data;
      });
    },
    onSubmit() {
      // 搜索按钮  // 学院列表
      let keywords = {
        keywords: this.formInline.user,
        rows: this.pageSize,
        page: this.currentPage1
      };
      this.$post("/basic/academy/list", keywords).then(res => {
        if (res.status == 1) {
          this.loading = false;
          this.tableData1 = res.data;
          this.total = res.total;
        }
      });
    },
    // list() {
    //     let keywords = {
    //         rows: this.pageSize,
    //         page: this.currentPage1
    //     }
    //     this.$post('/basic/academy/list', keywords).then(res => {
    //         if (res.status == 1) {
    //             this.loading = false
    //             this.tableData1 = res.data
    //             this.total = res.total
    //         }
    //     })
    // },
    // 新增列表
    academyAdd() {
      let name = this.form.name;
      let code = this.form.code;
      let intro = this.form.intro;
      let academyType = this.form.creatorVal;
      this.$post("/basic/academy/add", { name, code, intro, academyType }).then(
        res => {
          if (res.status == 1) {
            this.$notify({
              message: "新增成功",
              type: "success"
            });
            this.tableData1.unshift(res.data);
            this.onSubmit();
          } else {
            this.$notify({
              message: res.message,
              type: "warning"
            });
            // this.form = res.message
          }
        }
      );
    },
    handleEdit(index, row) {
      // 编辑对话框显示值
      this.tianjia = "编辑学院/部门";
      this.falg1 = false;
      this.falg = true;
      this.dialogFormVisible = true;
      this.form.name = row.name;
      this.form.code = row.code;
      this.form.intro = row.intro;
      this.form.creatorVal = row.academyType; // 编辑 部门 获取后台返回id
      this.form.id = row.id;
    },
    close() {
      this.tianjia = "添加学院/部门";
      this.falg1 = true;
      this.falg = false;
      this.dialogFormVisible = true;
      this.form.name = "";
      this.form.code = "";
      this.form.intro = "";
      this.form.creatorVal = "";
    },
    seccons(formName) {
      // 确定按钮的点击提交编辑内容
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.academyAdd();
          this.dialogFormVisible = false;
          this.onSubmit();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    seccons1(ruleForm) {
      // 确定按钮的点击提交编辑内容
      this.$refs[ruleForm].validate(valid => {
        if (valid) {
          let name = this.form.name;
          let code = this.form.code;
          let intro = this.form.intro;
          let id = this.form.id;
          let academyType = this.form.creatorVal;
          this.$put("/basic/academy/edit", {
            intro,
            name,
            code,
            id,
            academyType
          }).then(res => {
            if (res.status == 1) {
              this.onSubmit();
              this.$notify.success({
                title: "success",
                message: "编辑成功"
              });
              this.$refs[ruleForm].resetFields();
              this.dialogFormVisible = false;
            } else {
              this.$notify.warning({
                title: "warning",
                message: res.message
              });
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.dialogFormVisible = false;
      this.form = {};
    },
    handleClose() {
      this.$refs["ruleForm"].resetFields();
      this.dialogFormVisible = false;
      this.form = {};
    },

    handleDelete(index, row) {
      // 删除列表信息--------删除接口--row对象 /basic/academy/del/{id}
      this.$confirm("确定删除该信息?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$deletes(`/basic/academy/del/${row.id}`).then(res => {
            if (res.status == 1) {
              this.$notify.success({
                title: "success",
                message: "删除成功"
              });
              this.onSubmit();
            } else if (res.status == 0) {
              this.$notify.warning({
                title: "warning",
                message: res.message
              });
            }
          });
        })
        .catch(() => {
          this.$notify({
            type: "info",
            message: "已取消删除"
          });
        });
    },

    indexMethod(index) {
      return index * 1;
    },
    handleSizeChange(val) {
      this.pageSize = val;
      this.onSubmit();
    },
    handleCurrentChange(val) {
      this.currentPage1 = val;
      this.onSubmit();
    }
  },
  mounted() {
    this.onSubmit();
    //this.academyType();
  }
};
</script>

<style lang="scss" >
.CollegeInformation {
  margin-bottom: 40px;
  
  .el-form-item__label{
    font-size:14px;
    font-family:PingFang SC;
    font-weight:bold;
    color:rgba(105,105,105,1);
  }
  .CollegeInformation-inp {
    // margin-top: 18px;
    // float: left;
    overflow: hidden;
    margin-bottom: 20px;
    padding: 20px 20px 0;
    background-color: #fff;
    border-radius: 10px;
  }

  .CollegeInformation-btn {
    margin-top: 9px;
    // float: right;
  }
  .block {
    // float: left;
    width: 100%;
    text-align: right;
    padding: 20px;
    background-color: #fff;
    border-radius:  0 0 10px 10px;
    box-sizing: border-box;
    margin-bottom: 100px;
  }
}
</style>



